@use "./reset";
@use "./mixin" as m;
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;500;700;900&family=Roboto:wght@300;400;500;700;900&display=swap");

// Color
$backgroundColor: #1b1b1b;
$normalColor: #c7d0c6;
$appealColor: #00c060;
$neonColor: #19ff8c;

// Font Size
$fontSizeSmall: 1.4rem;
$fontSizeNormal: 1.8rem;
$fontSizeLarge: 4.2rem;

$teamTextSize: 16rem;
$teamTextSubSize: 6.4rem;

* {
  font-family: "Roboto", "Noto Sans JP", sans-serif;
  font-size: $fontSizeNormal;
}

html {
  font-size: 10px;
  background-color: $backgroundColor;
  color: $normalColor;
}

body {
  overflow-x: hidden;
}

section {
  position: relative;
  min-height: 100%;
  min-width: 100%;
  height: 100%;
  &:nth-of-type(n + 2) {
    @include m.mq(tab) {
      min-height: auto;
      height: auto;
    }
  }
  &:nth-last-of-type(1) {
    @include m.mq(tab) {
      min-height: 100%;
      height: 100%;
    }
  }
}
